.photo {
  object-fit: cover;
  height: 231px;
  position: relative;
}

.img {
  border-radius: 50%;
  width: 200px;
}

.people {
  color: #2f4f4f;
  text-align: center;
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  font-family: Arial, sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 23px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  border-radius: 12px;
  transition-property: transform, border-radius;
  transition-duration: 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .img {
  border-radius: 60px;
  transition-duration: 1s;
}

.person:hover .extra {
  visibility: visible;
}

.extra {
  visibility: hidden;
}

.rank {
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 32px;
  position: absolute;
  top: 84px;
  left: 158px;
}
